<template>
    <div>
        <h2>Character list</h2>
        <div v-for="(char, key) in roster" :key="key">
            {{ char.nickname }}
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return {
            roster: null
        }
    },
    created(){
        const that = this 
        axios.get('/admin/persona')
            .then(res => {
                that.roster = res.data
            })
            .catch(err => {
                alert(err)
            })
    }
}
</script>